.bg-blur {
  @apply bg-contrast/80 backdrop-blur;
}

.brick {
  @apply shadow-lg rounded-lg hover:shadow-sm transition-all hover:translate-y-1 h-full;
}

.brick .brick-image {
  @apply transition-all min-h-[240px] w-full bg-[length:100%_auto] bg-center rounded;
}

.brick:hover .h1,
.brick:hover .h2,
.brick:hover .h3,
.brick:hover .h4,
.brick:hover .h5,
.brick:hover .h6 {
  @apply text-secondary;
}

.brick:hover .h1 svg .svgText, .brick:hover .h2 svg .svgText, .brick:hover .h3 svg .svgText, .brick:hover .h4 svg .svgText, .brick:hover .h5 svg .svgText, .brick:hover .h6 svg .svgText {
  @apply fill-secondary stroke-[25px];
}

.brick:hover .brick-image {
  @apply bg-[length:110%_auto];
}

.brick .h1,
.brick .h2,
.brick .h3,
.brick .h4,
.brick .h5,
.brick .h6 {
  @apply -mt-8 transition-all;
}

.brick .h1 svg, .brick .h2 svg, .brick .h3 svg, .brick .h4 svg, .brick .h5 svg, .brick .h6 svg {
  @apply text-center w-full;
}

.brick .h1 svg .svgText, .brick .h2 svg .svgText, .brick .h3 svg .svgText, .brick .h4 svg .svgText, .brick .h5 svg .svgText, .brick .h6 svg .svgText {
  @apply fill-contrast stroke-white stroke-[15px] w-full font-bold transition-all;
  stroke-linejoin: round;
  paint-order: stroke;
}

.brick p {
  @apply paragraph-lg px-6 text-contrast/80 hover:text-contrast;
}

.brick-feature {
  @apply flex lg:flex-row flex-col flex-row mt-32 p-0 lg:px-8 xl:px-0;
}

.brick-feature.revert {
  @apply flex lg:flex-row-reverse flex-col flex-row;
}

.brick-feature.revert .brick-feature-text {
  @apply text-center lg:text-left;
}

.brick-feature.revert .brick-feature-image {
  @apply text-center lg:text-left;
}

.brick-feature .brick-feature-text {
  @apply lg:basis-1/3 lg:text-right p-8 basis-full m-auto  text-center;
}

.brick-feature .brick-feature-text.half {
  @apply lg:basis-1/2 basis-full;
}

.brick-feature .brick-feature-text_title {
  @apply h3 font-bold leading-[3rem];
}

.brick-feature .brick-feature-text_desc {
  @apply paragraph text-contrast self-center;
}

.brick-feature .brick-feature-image {
  @apply lg:basis-2/3 border border-solid border-neutral-300 rounded-xl overflow-hidden text-left basis-full;
}

.brick-feature .brick-feature-image.half {
  @apply lg:basis-1/2 basis-full;
}

.brick-feature .brick-feature-image.no-border {
  @apply border-none;
}

.brick-feature .brick-feature-image video {
  @apply w-full h-full object-cover;
}

.brick-feature .brick-feature-image > .img-brick {
  @apply xl:w-[1024px] hidden sm:block h-auto xl:h-[280px];
}

.brick-feature .brick-feature-image > .img-brick-mobile {
  @apply block sm:hidden;
}

.cursor {
  @apply cursor-pointer fixed z-50 left-0 top-0 pointer-events-none will-change-transform mix-blend-difference;
}

@media (hover: hover) and (pointer: fine) {
  .cursor .cursor__circle {
    @apply w-8 h-8 mt-[-50%] ml-[-50%] rounded-full border border-solid border-neutral-300/65;
    transition: opacity 0.3s cubic-bezier(0.25, 1, 0.5, 1), background-color 0.3s cubic-bezier(0.25, 1, 0.5, 1), border-color 0.3s cubic-bezier(0.25, 1, 0.5, 1), width 0.3s cubic-bezier(0.25, 1, 0.5, 1), height 0.3s cubic-bezier(0.25, 1, 0.5, 1);
  }
  .cursor.arrow {
    @apply after:content-[""] after:absolute after:left-[-50%] after:top-[-50%] after:w-full after:h-full after:bg-[url("https://svgshare.com/i/MzQ.svg")] after:bg-no-repeat after:bg-center;
  }
  .cursor.arrow .cursor__circle {
    @apply w-16 h-16 rounded-xl border-secondary cursor-pointer;
  }
  .cursor.subtle .cursor__circle {
    @apply opacity-20;
  }
  .cursor.overlay .cursor__circle {
    @apply w-6 h-6 bg-contrast/10 border-transparent;
  }
}
.container {
  @apply max-w-screen-2xl mx-auto relative h-full lg:px-8 px-0;
}